export default function (data, styles, sidebar) {


    function checkDefaultValue(codeTrim) {
         let defaultValue = '';
         sidebar.map((item) => {
             if ('object' !== typeof item.settings) {
                 return;
             }
             item.settings.map((setting) => {
                 if (setting.id === codeTrim) {
                     switch (setting.type){
                         case "select":
                             defaultValue = `/*!setting.${codeTrim}{*/${setting.options[0].value}/*}*/`;
                             break;
                         default:
                             defaultValue = `/*!setting.${codeTrim}{*/${setting.default}/*}*/`;
                             break;
                     }
                 }
             });
         });

         if(defaultValue){
             return defaultValue;
         }
    }

    return styles.replace(/(\{\{)(.*?)(\}\})/gi, (val) => {
        if (val) {
            let code = val.match(new RegExp("settings\.(.*?)\s*\}\}", "i")),
                codeTrim = code[1].trim();

            if (typeof codeTrim !== 'undefined' && codeTrim) {
                if (typeof data[codeTrim] !== 'undefined' && codeTrim) {
                    return `/*!setting.${codeTrim}{*/${data[codeTrim]}/*}*/`;
                } else {
                    return checkDefaultValue(codeTrim);
                }
            }
        }
    });


}




