import React, { Component } from 'react';
import {connect} from 'react-redux';
import Header from './header';
import $ from "jquery";

class Main extends Component {

    constructor(props) {
        super(props);
        this.state = {

        };

    }

    componentDidUpdate(){
        //console.log(this.props.store.style);
        let iframe = document.getElementById('customizer-iframe');
        let styleCode = this.props.store.style;

        iframe.addEventListener("load", function() {
            $('iframe').contents().find("#da")
                .html($("<style type='text/css'>"+styleCode+"</style>"));
        });
        $('iframe').contents().find("#da")
            .html($("<style type='text/css'>"+styleCode+"</style>"));
    }


    componentDidMount(){
        //console.log(this.props.store.style);
        let iframe = document.getElementById('customizer-iframe');
        let styleCode = this.props.store.style;

        iframe.addEventListener("load", function() {
            $('iframe').contents().find("#da")
                .html($("<style type='text/css'>"+styleCode+"</style>"));
        });

    }


    render() {

        const {responsive} = this.props.store;
        console.log(this.props.store, 'zagruzka');
        return (
            <div className="customizer-body">
                <Header />
                <div className="customizer-body__iframe-block">
                    <div className="customizer-body__iframe-wrapper" style={responsive === 'desktop' ? {maxWidth: "100%"} : {maxWidth: "375px", maxHeight: "650px"}}>
                        <textarea value={this.props.store.style} cols="30" rows="10" style={{position: "absolute", right: "999999px"}}/>
                        <iframe src={window.customizer.iframeUrl} id="customizer-iframe" frameBorder="0" className="customizer-body__iframe" />
                    </div>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return state;
}


export default connect(mapStateToProps)(Main);




