import React, { Component } from 'react';
import {connect} from 'react-redux';

import {changeResponsive} from '../actions/index';
import axios from 'axios';

class Header extends Component {

    constructor(props) {
        super(props);
        this.state = {

        };
    }

    handleChangeResponsive(value){
        const {dispatch} = this.props;
        dispatch(changeResponsive(value));
    }

    handleSave(){
        axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
        axios.defaults.xsrfCookieName = '_csrf-frontend';
        axios.post(window.customizer.saveUrl, this.props.store.data)
            .then(function (response) {
                console.log(response, 'save');
                alert(`zbs    ${response}`);
            })
            .catch(function (error) {
                console.log(error, 'error');
            });
    }

    render() {

        const {responsive} = this.props.store;
        const responsiveMenu = [
            {
                code: "desktop",
                icon: "fa fa-desktop"
            },
            {
                code: "mobile",
                icon: "fa fa-mobile"
            }
        ];

        return (
                <div className="customizer-body__header">
                    <div className="row">
                        <div className="col-sm-4">
                            <span className="customizer-theme-name">
                                Magic
                            </span>
                        </div>
                        <div className="col-sm-4 text-center">
                            <ul className="customizer-body__header-list">
                                {responsiveMenu.map((item, index) =>
                                    <li className={item.code === responsive ? 'active' : ''} key={`resp_${index}`} onClick={this.handleChangeResponsive.bind(this, item.code)}>
                                        <span className={`${item.icon}`} />
                                    </li>
                                )}
                            </ul>
                        </div>
                        <div className="col-sm-4 text-right">
                            <button className="btn btn-save" onClick={this.handleSave.bind(this)}>Save changes</button>
                        </div>
                    </div>
                </div>
        );
    }
}

function mapStateToProps(state) {
    return state;
}


export default connect(mapStateToProps)(Header);




