import React, { Component } from 'react';
import {connect} from 'react-redux';

import {changeText} from '../../actions/index';

class Text extends React.Component {

    render() {

        const {dispatch} = this.props;
        const {settings} = this.props;

        return (
            <div className="customizer-sidebar__settings-block">
                <div className="customizer-sidebar__settings-title">{settings.label}</div>
                <div className="customizer-sidebar__settings-description">
                    <input type="text" className="form-control form-control-sm" defaultValue={settings.default} onChange={event => dispatch(changeText(settings.id, event.target.value))}/>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return state;
}


export default connect(mapStateToProps)(Text);




