export default function (data, styles, sidebar) {
    sidebar.map((item) => {

        if (item.settings) {
            item.settings.map((setting) => {
                if(!data[setting.id] || data[setting.id] === undefined) {
                    switch (setting.type) {
                        case "select":
                            data[setting.id] = setting.options[0].value;
                            break;
                        default:
                            data[setting.id] = setting.default;
                            break;
                    }

                }
            });
        }
    });
    return data;
}



