import {
    INIT_STYLE,
    CHANGE_RESPONSIVE,
    IS_LOADER,
    INIT_SETTINGS,
    INIT_DATA,
    INIT_REGEXP_STYLES,
    CHANGE_COLOR,
    CHANGE_SELECT,
    CHANGE_RANGE_SLIDER,
    CHANGE_BACKGROUND,
    REMOVE_IMAGE,
    CHANGE_NUMBER,
    CHANGE_TEXT
} from '../constants/index';

import initStyles from '../helper/regexp_styles';
import checkDefaultData from '../helper/check_default_data';
import replaceStyle from '../helper/replace_style';

const initialState = {
    isLoader: false,
    data: {},
    style: '',
    responsive: 'desktop',
    sidebar: [],
};

export default function modals(state = initialState, action) {

    switch (action.type){
        case INIT_DATA:
            return {
                ...state,
                data: action.payload
            };

        case INIT_SETTINGS:
            return {
                ...state,
                sidebar: [
                    ...action.payload
                ]
            };

        case INIT_STYLE:
            return{
                ...state,
                style: action.payload
            };


        case INIT_REGEXP_STYLES:
            return {
                ...state,
                style: initStyles(action.payload.data, action.payload.styles, action.payload.sidebar),
                data: checkDefaultData(action.payload.data, action.payload.styles, action.payload.sidebar)
            };

        case CHANGE_RESPONSIVE:
            return{
                ...state,
                responsive: action.payload
            };

        case IS_LOADER:

            return{
                ...state,
                isLoader: action.payload
            };


        case CHANGE_COLOR:
        case CHANGE_RANGE_SLIDER:
        case REMOVE_IMAGE:
        case CHANGE_SELECT:
        case CHANGE_BACKGROUND:
        case CHANGE_NUMBER:
        case CHANGE_TEXT:
            return{
                ...state,
                style: replaceStyle(`${action.payload.id}`, state.style, state.data[action.payload.id], action.payload.data),
                data: {
                    ...state.data,
                    [action.payload.id]: action.payload.data
                }
            };

    }

    return state;
};



