import { SketchPicker } from 'react-color';
import React, { Component } from 'react';
import {connect} from 'react-redux';
import { changeColor } from '../../actions/index';

class ColorPicker extends React.Component {

    constructor(props) {
        super(props);

        this.handleClick = this.handleClick.bind(this);
        this.handleOutsideClick = this.handleOutsideClick.bind(this);

        this.state = {
            displayColorPicker: false
        };
    }

    handleClick() {
        if (!this.state.displayColorPicker) {
            document.addEventListener('click', this.handleOutsideClick, false);
        } else {
            document.removeEventListener('click', this.handleOutsideClick, false);
        }

        this.setState(prevState => ({
            displayColorPicker: !prevState.displayColorPicker,
        }));
    }

    handleOutsideClick(e) {
        if(this.node) {
            if (this.node.contains(e.target)) {
                return;
            }
        }

        this.handleClick();
    }

    handleChange(color){
        const {settings} = this.props;
        const {dispatch} = this.props;
        dispatch(changeColor(settings.id, color.hex));
    }




    render() {
        const {settings} = this.props;
        const {displayColorPicker} = this.state;
        const {data} = this.props.store;

        return (
            <div className="customizer-sidebar__settings-block">
                <div className="customizer-sidebar__settings-title">{settings.label}</div>
                <div className="customizer-sidebar__settings-description">
                    <div className="customizer-sidebar__color-block" onClick={this.handleClick.bind(this)}>
                        <div className="customizer-sidebar__color-picker" style={{ backgroundColor: data[settings.id] }}/>
                    </div>
                    {displayColorPicker ? <div className="customizer-sidebar__sketch-picker" ref={node => { this.node = node; }}>
                        <SketchPicker color={ data[settings.id] } onChange={this.handleChange.bind(this) } />
                    </div> : null}
                </div>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return state;
}


export default connect(mapStateToProps)(ColorPicker);




