import React, { Component } from 'react';
import Iframe from 'react-iframe'
import axios from 'axios';
import {connect} from 'react-redux';


import SlideBar from './components/sidebar';
import Main from './components/main';

import {loader, isLoader, initRegExpStyles, initRegExpData} from './actions/index';



class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            test: false,
            requests: {
                data: "http://www.mocky.io/v2/5b56d99231000062164d1f0d",
                styles: "http://www.mocky.io/v2/5b44c2ee2f00005400420a53?mocky-delay=100ms"
            }
        };
    }


    componentDidMount(){
        const {dispatch} = this.props;

        if(window.customizer.dataUrl && window.customizer.stylesUrl) {
            axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
            axios.defaults.xsrfCookieName = '_csrf-frontend';
            axios.post(window.customizer.dataUrl)
                .then(res => {
                    dispatch(loader('settings', res.data.settings));
                    dispatch(loader('data', res.data.data));

                    if (this.props.store.data && this.props.store.sidebar) {
                        axios.post(window.customizer.stylesUrl)
                            .then(res => {
                                dispatch(loader('styles', res.data));
                                dispatch(initRegExpStyles(this.props.store.data, this.props.store.style, this.props.store.sidebar));
                                dispatch(isLoader(true));
                            }).catch(error => {

                        });
                    }
                }).catch(error => {

            });
        }
    }



  render() {

      const {isLoader} = this.props.store;
      console.log(this.props.store);

      if(isLoader) {
          return (
              <div className="customizer-container">
                  <SlideBar />
                  <Main />
              </div>
          );
      }else{
          return(
              <div className="d-flex align-items-center justify-content-center h-100 w">
                  <span>Loading...</span>
              </div>
          )
      }
  }
}

function mapStateToProps(state) {
    return state;
}

export default connect(mapStateToProps)(App);




