import React, { Component } from 'react';
import {connect} from 'react-redux';
import Slider from 'react-rangeslider'
import 'react-rangeslider/lib/index.css'

import {changeRangeSlider} from '../../actions/index';

class RangeSlider extends React.Component {

    render() {

        const {dispatch} = this.props;
        const {settings} = this.props;

        return (
        <div className="customizer-sidebar__settings-block">
            <div className="customizer-sidebar__settings-title">{settings.label}</div>
            <div className="customizer-sidebar__settings-description">
                <Slider
                    max={46}
                    min={10}
                    value={parseInt(this.props.store.data[settings.id])}
                    onChange={event =>{dispatch(changeRangeSlider(settings.id, event))}}
                />
            </div>
        </div>
        );
    }
}

function mapStateToProps(state) {
    return state;
}


export default connect(mapStateToProps)(RangeSlider);




