import React, { Component } from 'react';
import {connect} from 'react-redux';

import {changeSelect} from '../../actions/index';

class Select extends React.Component {

    render() {

        const {dispatch} = this.props;
        const {settings} = this.props;

        return (
            <div className="customizer-sidebar__settings-block">
                <div className="customizer-sidebar__settings-title">{settings.label}</div>
                <div className="customizer-sidebar__settings-description">
                    <select className="form-control form-control-sm" onChange={event => dispatch(changeSelect(settings.id, event.target.value))}>
                        {settings.options.map((item, index) =>
                            <option value={item.value} key={`select_${index}`}>{item.label}</option>
                        )}
                    </select>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return state;
}


export default connect(mapStateToProps)(Select);




