import {
    CHANGE_SELECT,
    CHANGE_RANGE_SLIDER,
    INIT_STYLE,
    INIT_DATA,
    INIT_SETTINGS,
    CHANGE_RESPONSIVE,
    IS_LOADER,
    INIT_REGEXP_STYLES,
    INIT_REGEXP_DATA,
    CHANGE_COLOR,
    CHANGE_BACKGROUND,
    REMOVE_IMAGE,
    CHANGE_NUMBER,
    CHANGE_TEXT
} from '../constants/index';


export const loader = (type, data) => {

    switch (type){
        case 'styles':
            return {
                type: INIT_STYLE,
                payload: data
            };
            break;
        case 'settings':
            console.log(data);
            return {
                type: INIT_SETTINGS,
                payload: data
            };
            break;

        case 'data':
            return {
                type: INIT_DATA,
                payload: data
            };
            break;
    }
};

export const isLoader = (load) => {

    return {
        type: IS_LOADER,
        payload: load
    }
};


export const initRegExpStyles = (data, styles, sidebar) => {

    return {
        type: INIT_REGEXP_STYLES,
        payload: {
            data: data,
            styles: styles,
            sidebar: sidebar
        }
    }
};

export const initRegExpData = (data, sidebar) => {

    return {
        type: INIT_REGEXP_DATA,
        payload: {
            data: data,
            sidebar: sidebar
        }
    }
};


export const changeResponsive = (value) =>{
    return {
        type: CHANGE_RESPONSIVE,
        payload: value.toLowerCase()
    }
};

export const changeSelect = (id, data) => {

    return {
        type: CHANGE_SELECT,
        payload: {
            id: id,
            data: data
        }
    }
};

export const changeRangeSlider = (id, data) => {
    return {
        type: CHANGE_RANGE_SLIDER,
        payload: {
            id: id,
            data: `${data}px`
        }
    }
};

export const changeColor = (id, data) => {
    return {
        type: CHANGE_COLOR,
        payload: {
            id: id,
            data: data
        }
    }
};

export const changeBackground = (id, data) => {
    return {
        type: CHANGE_BACKGROUND,
        payload: {
            id: id,
            data: data
        }
    }
};

export const removeImage = (id, data) => {
    return {
        type: REMOVE_IMAGE,
        payload: {
            id: id,
            data: data
        }
    }
};


export const changeNumber = (id, data) => {
    return {
        type: CHANGE_NUMBER,
        payload: {
            id: id,
            data: data
        }
    }
};

export const changeText = (id, data) => {
    return {
        type: CHANGE_TEXT,
        payload: {
            id: id,
            data: data
        }
    }
};


