import React, { Component } from 'react';
import {connect} from 'react-redux';

import {changeBackground, removeImage} from '../../actions/index';
import axios from 'axios';


class Background extends React.Component {


    handleUpload(e){
        if(e.target.files[0]) {
            const {dispatch} = this.props;
            const {settings} = this.props;
            const fd = new FormData();

            fd.append('image', e.target.files[0], e.target.files[0].name);

            axios.post('http://www.mocky.io/v2/5b0c06ab3300006800b3fed5', fd)
                .then(res => {
                    dispatch(changeBackground(settings.id, res.data.url));
                }).catch(error => {

            })
        }
    }

    render() {

        const {dispatch} = this.props;
        const {settings} = this.props;
        const {data} = this.props.store;

        return (
            <div className="customizer-sidebar__settings-block">
                <div className="customizer-sidebar__settings-title">{settings.label}</div>
                <div className="customizer-sidebar__settings-description">
                    {data[settings.id] !== 'none' ?
                        <div className="customizer-sidebar__setting-background-remove"
                             onClick={event => dispatch(removeImage(settings.id, 'none'))}>
                            <span className="fa fa-times"/>
                        </div> : null
                    }
                    <label htmlFor={`upload-file_${settings.id}`} className={`customizer-sidebar__setting-background ${data[settings.id] === 'none' ? '' : 'customizer-sidebar__setting-active'}`}>
                        {data[settings.id] === 'none' ? <div className="customizer-sidebar__background-upload">
                                <span className="fa fa-cloud-upload"/>
                                <input type="file" id={`upload-file_${settings.id}`} className="customizer-sidebar__background-upload-input" onChange={event => {this.handleUpload(event)}}/>
                                Upload
                            </div> :
                          <div>
                              <input type="file" id={`upload-file_${settings.id}`} className="customizer-sidebar__background-upload-input" onChange={event => {this.handleUpload(event)}}/>
                              <img src={data[settings.id]} className="img-fluid" />
                          </div>
                        }
                    </label>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return state;
}


export default connect(mapStateToProps)(Background);




