import React, { Component } from 'react';
import {connect} from 'react-redux';

import ColorPicker from './elements/color_picker';
import Select from './elements/select';
import RangeSlider from './elements/slider';
import Background from './elements/background';
import Text from './elements/text';

import Switch, { Case, Default } from 'react-switch-case';

class SlideBar extends Component {

    constructor(props) {
        super(props);
        this.state = {
            active: 0
        };

    }


    handleChangeTab(index){
        this.setState({
            active: index
        })
    }

    render() {

        const {sidebar} = this.props.store;
        const {active} = this.state;

        let options = {

        };

        return (
            <div className="customizer-sidebar">
                <div className="customizer-sidebar__header">
                    <a href="/admin/settings/themes">
                        <span className="fa fa-angle-left" />
                    </a>
                    <span className="customizer-sidebar__header-title">Customizer</span>
                </div>
                <div className="customizer-sidebar__block">

                    {sidebar.map((item, index) =>
                        <div className="customizer-sidebar__category" key={`sidebar_${index}`}>
                            <div className="customizer-sidebar__category-title" onClick={this.handleChangeTab.bind(this, index)}>
                                <span className={`${item.icon}`}/>
                                {item.name}
                                <span className={`fa ${active === index ? 'fa-angle-up' : 'fa-angle-down'}`} />
                            </div>



                            {item.settings && index === active ?

                                <div className="customizer-sidebar__category-body">
                                            {item.settings.map((setting, setting_index) =>
                                                <Switch condition={setting.type} key={`settings_${setting_index}`}>
                                                    <Case value='color'>
                                                        <ColorPicker settings={setting} />
                                                    </Case>
                                                    <Case value='select'>
                                                        <Select settings={setting} />
                                                    </Case>
                                                    <Case value='rangeslider'>
                                                        <RangeSlider settings={setting} />
                                                    </Case>
                                                    <Case value='background'>
                                                        <Background settings={setting} />
                                                    </Case>
                                                    <Case value='text'>
                                                        <Text settings={setting} />
                                                    </Case>
                                                </Switch>
                                            )}
                                </div> : <div />
                            }

                        </div>
                    )}

                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return state;
}


export default connect(mapStateToProps)(SlideBar);




